<template>
  <div class="home">
    <Tomato_clock @menuTrigger="showMenu" />
    <transition name="slide" mode="out-in">
      <Status v-if="menu_cover" :status="status" @leaveStatus="hideMenu" />
    </transition>
  </div>
</template>

<script>
import Tomato_clock from "@/components/Tomato_clock.vue";
import Status from "@/views/Status.vue";

export default {
  name: "home",
  data() {
    return {
      menu_cover: false,
      status: ""
    };
  },
  components: {
    Tomato_clock,
    Status
  },
  methods: {
    showMenu(trigger_status) {
      this.menu_cover = true;
      this.status = trigger_status;
    },
    hideMenu() {
      this.menu_cover = false;
    }
  }
};
</script>
<style lang="scss" scoped>
.home {
  box-sizing: border-box;
}
.slide-enter-active {
  animation: slideInRight 0.3s;
}
.slide-leave-active {
  animation: slideOutRight 0.3s;
}

@keyframes slideInRight {
  0% {
    -webkit-transform: translate3d(100%, 0, 0);
    transform: translate3d(100%, 0, 0);
    visibility: visible;
    opacity: 0;
  }

  to {
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
    opacity: 1;
  }
}

@keyframes slideOutRight {
  0% {
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
    opacity: 1;
  }

  to {
    visibility: hidden;
    -webkit-transform: translate3d(100%, 0, 0);
    transform: translate3d(100%, 0, 0);
    opacity: 0;
  }
}
</style>
